@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';
@import '../../assets/styles/core/common';

.icon_location2 {
  position: absolute;
  top: pr2(4);
  left: 0;
  display: inline-block;
  width: pr2(17);
  height: pr2(17);
  background: url('data:image/png;base64,') no-repeat;
  background-size: 100% 100%;
  // transform: translateY(-50%);
}

.service_item {
  padding-top: pr2(16);
  margin: 0 pr2(20);
  .name {
    font-size: pr2(22);
    line-height: 1;
    color: #111E36;
    font-family: PingFangSC-Medium;
  }
  .price_item {
    padding-top: pr2(12);
    text-align: right;
    font-size: pr2(14);
    line-height: 1;
    font-family: PingFangSC-Regular;
    color: #FF6600;
  }
  .nums {
    font-family: YCZJ-DINAlternate;
    font-size: pr2(24);
  }
}


.infos_item {
  margin: 0 pr2(20);
  margin-top: pr2(12);
  padding: 0 pr2(20);
  @extend %shadow;
  border-radius: 6px;
  .cell {
    padding: pr2(13) 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    color: #111E36;
    line-height: pr2(24);
    &.times_cell {
      padding-top: 0;
      padding-bottom: pr2(8);
      .cell_hd {
        flex: 1;
        padding: 0 pr2(10);
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

      }
    }
    &.input_item {

    }
  }
  .cell_hd {
    font-size: pr2(16);
    // line-height: 1;
    font-family: PingFangSC-Medium;
    min-width: pr2(66);
    margin-right: pr2(8);
  }
  .cell_bd {
    display: flex;
    align-items: center;
    font-size: pr2(16);
    // line-height: 1;
    font-family: PingFangSC-Regular;
    &.dealer_name {
      max-height: pr2(48);
      @include ellipsisLn(2);
    }
    &.dealer_address {
      position: relative;
      padding-left: pr2(21);
      max-height: pr2(72);
      @include ellipsisLn(3);
    }
  }
  .icon_arrow_r {
    @extend %icon_arrow_r;
    margin-left: pr2(6);
  }
  .time {
    width: pr2(108);
    margin-bottom: pr2(8);
    box-sizing: border-box;
    text-align: center;
    position: relative;
    padding: pr2(13) 0;
    color: #0055FF;
    font-size: pr2(14);
    font-family: PingFangSC-Regular;
    border-radius: 5px;
    &.active {
      color: #fff;
      background: #0055FF;
    }
    &:before {
      content: " ";
      width: 200%;
      height: 200%;
      position: absolute;
      top: 0;
      left: 0;
      border: 1px solid rgba(0, 85, 255, 0.5);;
      transform: scale(0.5);
      transform-origin: 0 0;
      box-sizing: border-box;
      border-radius: 10px;
    }
  }
  input {
    display: inline-block;
    outline: none;
    border: 0;
    padding: 0;
    margin: 0;
    text-align: right;
  }
}

.submit_btn {
  line-height: pr2(50);
  margin: pr2(34) pr2(20);
  background: #0055FF;
  color: #fff;
  font-size: pr2(16);
  font-family: PingFangSC-Regular;
  text-align: center;
  border-radius: 4px;
  &.disabled {
    background:#F1F3F6;
    color: #C5CAD4;
  }
}